* {
  box-sizing: border-box;
}

:root {
  font-size: 100%; // temp sanitize.css bug fix
  overflow-x: hidden;
  word-wrap: break-word;
}

input {
  &[type='radio'] {
    border: 0.0625rem solid $gray;
    border-radius: 0.5rem;
    background-color: $white;

    &:checked {
      background: $black;
    }
  }

  &[type='search'] {
    appearance: none;
  }
}

button {
  cursor: pointer;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

svg {
  fill: currentColor;
}

